import React from "react";
import { useState } from "react";

import { useDispatch, useSelector } from "react-redux";
import CheckOutSteps from "../components/CheckOutSteps";
import { savePaymentMethod } from "../actions/cartAction";
import { MdPayment } from "react-icons/md";
import { FaBackward } from "react-icons/fa";
import { Link } from "react-router-dom";

const Payment = ({ history }) => {
  const dispatch = useDispatch();
  const { shippingAddress } = useSelector((state) => state.cart);

  if (!shippingAddress) {
    history.pust("/shipping");
  }

  const [paymentMethod, setPaymentMethod] = useState("cashOnDelivery");

  const submitHandler = (e) => {
    e.preventDefault();
    dispatch(savePaymentMethod(paymentMethod));
    history.push("/placeOrder");
  };

  return (
    <>
      <section className=" bg-gray-50 text-gray-600 body-font relative">
        <div className="container px-5 py-10 mx-auto">
          <div className="">
            <Link to="/shipping">
              {" "}
              <FaBackward color="blue" />{" "}
            </Link>
          </div>
          <div className="flex flex-col items-center w-full mb-12">
            <h1 class="sm:text-3xl text-2xl  inline-flex  items-center font-medium title-font mb-4 text-gray-700">
              Payment <MdPayment className="mx-1" color="blue" />{" "}
            </h1>
          </div>
          <CheckOutSteps step1 step2 step3 />

          <form className="py-10" onSubmit={submitHandler}>
            <div className="lg:w-1/2 md:w-2/3 mx-auto">
              <div className="flex flex-wrap -m-2">
                <div className="p-2 w-full">
                  <div className="relative">
                    <label
                      for="email"
                      className="leading-7 text-sm text-gray-600"
                    >
                      Payment Method
                    </label>
                    <input
                      type="check"
                    //   checked
                      disabled
                      id="cashOnDelivery"
                      value="cashOnDelivery"
                      name="cashOnDelivery"
                      onChange={(e) => setPaymentMethod(e.target.value)}
                      className="w-full bg-gray-100 bg-opacity-50 rounded border border-gray-300 focus:border-indigo-500 focus:bg-white focus:ring-2 focus:ring-indigo-200 text-base outline-none text-gray-700 py-1 px-3 leading-8 transition-colors duration-200 ease-in-out"
                    />
                  </div>
                </div>

                <div className=" p-2 w-full" onClick={submitHandler}>
                  <button className="flex mx-auto text-white   bg-indigo-500 border-0 py-2 px-8 focus:outline-none hover:bg-indigo-600 rounded text-lg">
                    Continue
                  </button>
                </div>
              </div>
            </div>
          </form>
        </div>
      </section>
    </>
  );
};

export default Payment;
