import React from "react";
import { BiLogIn } from "react-icons/bi";
import { FaAddressBook } from "react-icons/fa";
import { MdPayment } from "react-icons/md";
import { RiOrderPlayFill } from "react-icons/ri";
import { Link } from "react-router-dom";

const CheckOutSteps = ({ step1, step2, step3, step4 }) => {
  return (
    <div
      style={{
        display: "flex",
        justifyContent: "center",
        alignItems: "center",
        gap: "20px",
      }}
    >
      <div>
        {step1 ? (
          <button
            style={{
              display: "flex",
              flexDirection: "row",
              justifyContent: "center",
              alignItems: "center",
            }}
          >
            <Link to="/login">Login</Link>
            <BiLogIn color="blue" />
          </button>
        ) : (
          <button
            disabled
            style={{
              display: "flex",
              flexDirection: "row",
              justifyContent: "center",
              alignItems: "center",
            }}
          >
            <Link> signIn </Link> <BiLogIn color="gray" />
          </button>
        )}
      </div>

      <div>
        {step2 ? (
          <button
            style={{
              display: "flex",
              flexDirection: "row",
              justifyContent: "center",
              alignItems: "center",
            }}
          >
            <Link to="/shipping">Address</Link>
            <FaAddressBook color="blue" />
          </button>
        ) : (
          <button
            disabled
            style={{
              display: "flex",
              flexDirection: "row",
              justifyContent: "center",
              alignItems: "center",
            }}
          >
            Address <FaAddressBook color="gray" />
          </button>
        )}
      </div>

      <div>
        {step3 ? (
          <button
            style={{
              display: "flex",
              flexDirection: "row",
              justifyContent: "center",
              alignItems: "center",
            }}
          >
            <Link to="/payment">Payment</Link>
            <MdPayment color="blue" />
          </button>
        ) : (
          <button
            disabled
            style={{
              display: "flex",
              flexDirection: "row",
              justifyContent: "center",
              alignItems: "center",
            }}
          >
            Payment <MdPayment color="gray" />
          </button>
        )}
      </div>

      <div>
        {step4 ? (
          <button
            style={{
              display: "flex",
              flexDirection: "row",
              justifyContent: "center",
              alignItems: "center",
            }}
          >
            <Link to="/placeOrder">Place Order</Link>
            <RiOrderPlayFill color="blue" />
          </button>
        ) : (
          <button
            disabled
            style={{
              display: "flex",
              flexDirection: "row",
              justifyContent: "center",
              alignItems: "center",
            }}
          >
            Place Order <RiOrderPlayFill color="gray" />
          </button>
        )}
      </div>

    
  
    </div>
  );
};

export default CheckOutSteps;
